@import "../../styles/all.css";

.root {
  width: 100%;
  min-width: 450px;
  padding-bottom: $spacing-36;

  @include Breakpoint-mobileOnly {
    width: 100%;
    max-width: 350px;
    margin: 0 auto;
  }
}

.container {
  display: grid;
  grid-template-columns: 310px 1fr;

  box-sizing: border-box;

  width: 100%;

  min-width: 450px;
  min-height: 42vw;

  border-radius: $spacing-12;

  box-shadow: color($mirage alpha(75%)) 0px 0px 10px;

  @include Breakpoint-mobileOnly {
    grid-template-columns: 100%;
    width: 100%;
    height: 100%;
    max-width: 350px;
    margin: 0 auto;
  }
}

.options-container {
  height: 100%;

  background-color: $pickled-bluewood;

  @include Breakpoint-mobileOnly {
    order: 2;

    width: 100%;
  }
}

.game-container {
  position: relative;

  display: block;

  width: 100%;
  height: 100%;

  @include Breakpoint-mobileOnly {
    order: 1;

    min-height: 300px;
  }
}

.history {
  position: absolute;

  top: 0;

  right: 0;

  width: 100%;

  overflow: hidden;
}

.sound {
  padding-top: $spacing-8;
  padding-right: $spacing-8;

  button {
    margin: 0 0 0 auto;
  }
}
