@import "../../styles/all.css";

.root {
  text-align: center;
}

.container {
  display: grid;
  justify-content: center;

  grid-column-gap: $spacing-28;
  grid-row-gap: $spacing-28;

  grid-template-columns: repeat(3, 290px);

  @include Breakpoint-tabletOnly {
    grid-template-columns: 1fr;
  }

  @include Breakpoint-mobileOnly {
    grid-template-columns: 1fr;
  }
}

.coin {
  background-color: $tree-poppy;

  border-radius: 50%;

  box-shadow: inset 0 0 15px color($red alpha(50%));
}
