@import "../../styles/reset.css";
@import "../../styles/all.css";

.container {
  max-width: $content-width-desktop;

  @include Breakpoint-tabletAndAbove {
    max-width: $content-width-desktop;

    padding: calc($navbar-height + $spacing-36) $spacing-20 0 $spacing-20;
    margin: 0 auto;
  }

  @include Breakpoint-mobileOnly {
    width: 100%;

    max-width: $content-width-mobile;
    padding: calc($navbar-height + $spacing-36) 0 0 0;
    margin: 0 auto;
  }
}

.modal {
  display: flex;
  flex-direction: column;
  align-items: center;

  padding: $spacing-52 $spacing-20;

  .tabs {
    width: 100%;
  }
}
