@import "../../styles/all.css";

.h1 {
  font-family: $font-family;
  font-size: $font-size-h1;
  line-height: $line-height-h1;

  @include Breakpoint-mobileOnly {
    font-size: $font-size-h1-mobile;
    line-height: $line-height-h1-mobile;
  }
}

.h2 {
  font-family: $font-family;
  font-size: $font-size-h2;
  line-height: $line-height-h2;

  @include Breakpoint-mobileOnly {
    font-size: $font-size-h2-mobile;
    line-height: $line-height-h2-mobile;
  }
}

.h3 {
  font-family: $font-family;
  font-size: $font-size-h3;
  line-height: $line-height-h3;

  @include Breakpoint-mobileOnly {
    font-size: $font-size-h3-mobile;
    line-height: $line-height-h3-mobile;
  }
}

.h4 {
  font-family: $font-family;
  font-size: $font-size-h4;
  line-height: $line-height-h4;

  @include Breakpoint-mobileOnly {
    font-size: $font-size-h4-mobile;
    line-height: $line-height-h4-mobile;
  }
}

.body {
  font-family: $font-family;
  font-size: $font-size-body;
  line-height: $line-height-body;

  @include Breakpoint-mobileOnly {
    font-size: $font-size-body-mobile;
    line-height: $line-height-body-mobile;
  }
}

.small-body {
  font-family: $font-family;
  font-size: $font-size-small-body;
  line-height: $line-height-small-body;

  @include Breakpoint-mobileOnly {
    font-size: $font-size-small-body-mobile;
    line-height: $line-height-small-body-mobile;
  }
}

.xsmall-body {
  font-family: $font-family;
  font-size: $font-size-xsmall-body;
  line-height: $line-height-xsmall-body;

  @include Breakpoint-mobileOnly {
    font-size: $font-size-xsmall-body-mobile;
    line-height: $line-height-xsmall-body-mobile;
  }
}

.regular {
  font-weight: 400;
}

.semi-bold {
  font-weight: 600;
}

.bold {
  font-weight: 700;
}

.regular,
.semi-bold,
.bold {
  cursor: inherit;
}

.gable-green {
  color: $gable-green;
}

.pickled-bluewood {
  color: $pickled-bluewood;
}

.white {
  color: white;
}

.mercury {
  color: $mercury;
}

.casper {
  color: $casper;
}

.green {
  color: $green;
}

.red {
  color: $red;
}
